* {
    margin: 0;
    padding: 0;
}
.Navbar{
    display: flex;
    justify-content: space-between;
    padding: 1rem;
    background-color: rgb(53, 53, 54);
}
.Navbar h2 {
    color: white;
}
.Navbar div{
    display: flex;
    align-items: center;
    gap: 2rem;
}
.Navbar a{
    text-decoration: none;
    color: white;
}
.Search{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 3rem;
}
.Search input {
    width: 20vw;
    padding: 0.2rem;
}
.Search button {
    padding: 0.2rem;
}
.App {
    display: flex;
    flex-direction: column;
    gap: 2rem;
}
.Defination p{
    height: 50vh;
    width: 90vw;
    padding: 2rem;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}